.zoom-enter {
  transform: scale(0);
}
.zoom-enter-active {
  transform: scale(1);
  transition: transform 500ms ease-in;
}
.zoom-exit {
  transform: scale(1);
}
.zoom-exit-active {
  transform: scale(0);
  transition: transform 500ms ease-in;
}


.fade-enter {
  opacity: 0;
}
.fade-enter-active {
  opacity: 1;
  transition: opacity 500ms ease-in;
}
.fade-exit {
  opacity: 1;
}
.fade-exit-active {
  opacity: 0;
  transition: opacity 500ms ease-in;
}
